// GameManager.js
var GameMgr = pc.createScript('gameManager');

GameMgr.attributes.add('trackAlternateFrames', {
    type: 'boolean',
    default: false,
    title: 'Track Alternate Frames',
    description: 'If selected, tracking is only performed on every other update. This can increase lag in tracking but will reduce CPU load.'
});

GameMgr.attributes.add('HearthBeatMgr', { type: 'entity' });
GameMgr.attributes.add('ScreenResolutionText', { type: 'entity' });
GameMgr.attributes.add('TimeText', { type: 'entity' });
GameMgr.attributes.add('PulseText', { type: 'entity' });
GameMgr.attributes.add('BpmText', { type: 'entity' });
GameMgr.attributes.add('ScanWindowWidth', { type: 'number', default: 50});
GameMgr.attributes.add('ScanWindowHeight', { type: 'number', default: 50});
GameMgr.attributes.add('MiniWindowWidth', { type: 'number', default: 300});
GameMgr.attributes.add('MiniWindowHeight', { type: 'number', default: 300});
GameMgr.attributes.add('ArrayMaxCount', { type: 'number', default: 100});
GameMgr.attributes.add('BeatZone', { type: 'number', default: 0.5});
GameMgr.attributes.add('OutBeatZoneRange', { type: 'number', default: 2});
GameMgr.attributes.add('MinFramesForSound', { type: 'number', default: 5});
GameMgr.attributes.add('weight', {
    type: 'number',
    default: 0.5,
    min: 0,
    max: 1,
    title: 'Weight',
    description: 'Sensitivity, control shakiness'
});

GameMgr.attributes.add('GrayMode', { type: 'boolean', default: true});
GameMgr.attributes.add('DisplaySmooth', { type: 'boolean', default: false});

// initialize code called once per entity
GameMgr.prototype.initialize = function() {
    var app = this.app;
    var self = this;
    
    app.addTweenManager();
    
    this.process = true;
    this.openCVReady = false;
    
    this.timer = 0;
    this.posArray = [];
    this.timeArray = [];
    
    this.weightInv = 1-this.weight;
    this.oldLum = 0;
    
    this.goingUp = false;
    this.goingDown = false;
    this.framesForSound = 0;
    this.canPlaySound = true;
    this.BeatRangeFound = false;
    this.isBeatZone = false;
    this.beatCount = 0;
    this.timeStart = 0;
      
    if (this.supportsVideoMode())
        this.enterVideoMode();    
    
    app.on('game:beatAnimate', function () {
        this.beatCount++;
        if(this.canPlaySound) {
            this.app.fire('heart:pulse');
            this.entity.sound.play('beat');
            this.canPlaySound = false;
            setTimeout(function () {
                self.canPlaySound = true;
            }, 100);
        }
    }, this);
};

// update code called every frame
GameMgr.prototype.update = function(dt) {
  
    TWEEN.update();
    
    this.timer += dt;
    
    if(this.openCVReady) {
        this.cap.read(this.src);
        
        // Update the tracking
        if (this.trackAlternateFrames) {
            if (this.process)
                this.render();
            
            this.process = !this.process;
        } else {
            this.render();
        }
    }
    
    // If we're displaying video via a texture, copy the video frame into the texture
    if (this.texture) {
        this.texture.upload();
    }
};

GameMgr.prototype.render = function() {
    this.CheckHeartBeat();
};

GameMgr.prototype.CheckHeartBeat = function() {
    
    cv.cvtColor(this.src, this.dst, this.GrayMode ? cv.COLOR_RGB2GRAY : cv.COLOR_RGB2HSV);
    
    //get region of interest
    this.dst2 = this.dst.roi(this.rect);
    
    //smoothing on the pixels region
    cv.medianBlur(this.dst2, this.dst3, 15);
    
    //get average luminosity of this section
    var result = cv.mean(this.dst3);
    var lumAverage = result[this.GrayMode ? 0 : 2];
    
    //make canvas bigger for user display
    if(!this.DisplaySmooth) {
        cv.resize(this.dst2, this.dst2, this.outputSize, 0, 0, cv.INTER_AREA);
        cv.imshow('output', this.dst2);
    }
    else {
        cv.resize(this.dst3, this.dst3, this.outputSize, 0, 0, cv.INTER_AREA);
        cv.imshow('output', this.dst3);
    }
    
    //smoothing out the luminosity value when in beat zone
    var lum = lumAverage;
    var old = 0;
    lum = lum * this.weight;
    old = this.oldLum * this.weightInv; 
    
    var newLum = lum + old;
    
    //if we are in range, be more strict on variation
    var validValue = true;
    if(this.BeatRangeFound) {
        
        var span = this.timer - this.timeStart;
        this.BpmText.element.text = (Math.round((this.beatCount * 60 / span) * 100) / 100).toFixed(0) + ' BPM';
        
        
        var smaller = newLum <= this.highest;
        var higher = newLum >= this.lowest;
        
        validValue = smaller && higher;
        
        //value not in range
        if(!validValue) {
            if(higher) {
                this.BeatRangeFound = newLum - this.highest <= this.OutBeatZoneRange;
                if(!this.BeatRangeFound) {
                    newLum = this.highest;
                    this.beatCount = 0;
                }
            }
            else if(smaller) {
                this.BeatRangeFound = this.lowest - newLum <= this.OutBeatZoneRange;
                if(!this.BeatRangeFound) {
                    newLum = this.lowest;
                    this.beatCount = 0;
                }
            }
        }
    }
    
    //buildup data for graph
    if(this.posArray.length >= this.ArrayMaxCount) {
        this.posArray.shift();
        this.timeArray.shift();
    }  

    this.posArray.push(newLum);
    this.timeArray.push(this.timer);

    //stats
    var avgLight = (Math.round(newLum * 100) / 100).toFixed(2);
    var val2 = (Math.round(this.highest * 100) / 100).toFixed(2);
    var val3 = (Math.round(this.lowest * 100) / 100).toFixed(2);
    
    //try to extract some sense from the data
    this.checkFlow(newLum, this.oldLum);
    this.oldLum = newLum;
    
    //animate the graph
    this.HearthBeatMgr.script.heartBeatRenderer.setArrays(this.posArray, this.timeArray, this.lowest, this.highest, this.OutBeatZoneRange);
    
    //display infos
    this.TimeText.element.text = (Math.round(this.timer * 100) / 100).toFixed(2) + 'sec';
    this.PulseText.element.text = 'Avg. Luminosity : ' + avgLight + '\nHigh range : ' + val2 + '\nLow range : ' + val3 + (this.isBeatZone ? '\n\n    Looking for Pulse' : '\n\n   Gathering Data');
};

GameMgr.prototype.checkFlow = function (lum, oldLum) {
    var self = this;
    
    if(lum >= oldLum) {
        if(this.goingUp)
            ++this.framesForSound;
        
        this.goingUp = true;
        this.goingDown = false;
    }
    else  {
        if(this.isBeatZone && this.goingUp && this.framesForSound >= this.MinFramesForSound) {
            this.app.fire('game:beatAnimate');
        }   
          
        this.framesForSound = 0;
        this.goingUp = false;
        this.goingDown = true;
    }
    
    //stop computing range in beat zone
    if(!this.BeatRangeFound) {
        this.lowest = 255;
        this.highest = 0;

        for(var i = 0; i < this.posArray.length; ++i) {
            
            var val = this.posArray[i];
            if(val < this.lowest)
                this.lowest = val;
            if(val > this.highest)
                this.highest = val;
        }  
    } 
    
    //we want some data before checking if we are beatzone
    if(this.BeatRangeFound || this.posArray.length < 90)
        return;
    
    this.isBeatZone = ((this.highest - this.lowest) <= this.BeatZone);
    if(this.isBeatZone) {
        this.timeStart = this.timer;
        this.BeatRangeFound = true;
    }
};

GameMgr.prototype.startOpenCV = function () {
    var self = this;
    
    var script = document.createElement('script');
    script.onload = function() {
        console.log("OpenCV loaded and ready");
         
        cv.onRuntimeInitialized = function() {

            self.video.width = self.video.videoWidth;
            self.video.height = self.video.videoHeight;
            
            var vw = self.video.width;
            var vh = self.video.height;
            
            self.cap = new cv.VideoCapture(self.video);
            self.src = new cv.Mat(vh, vw, cv.CV_8UC4);
            self.dst = new cv.Mat(vh, vw, self.GrayMode ? cv.CV_8UC1 : cv.CV_8UC4);

            var ratio = vw / vh; 
            self.scanSize = new cv.Size(self.ScanWindowWidth, self.ScanWindowHeight);
            self.outputSize = new cv.Size(self.MiniWindowWidth, self.MiniWindowHeight);

            var wCenter = vw * 0.5;
            var hCenter = vh * 0.5;
            var wOffset = self.scanSize.width * 0.5;
            var hOffset = self.scanSize.height * 0.8;
            self.rect = new cv.Rect(wCenter - wOffset, hCenter - hOffset, self.scanSize.width, self.scanSize.height);
            self.dst2 = new cv.Mat(self.scanSize.height, self.scanSize.width, self.GrayMode ? cv.CV_8UC1 : cv.CV_8UC4);
            self.dst3 = new cv.Mat(self.scanSize.height, self.scanSize.width, self.GrayMode ? cv.CV_8UC1 : cv.CV_8UC4);
            
            self.ScreenResolutionText.element.text = 'Video feed : ' + vw + ' x ' + vh;
            
            self.openCVReady = true;
        };
    };
    
    script.src = "https://docs.opencv.org/4.5.2/opencv.js";
    document.getElementsByTagName('head')[0].appendChild(script);
};

GameMgr.prototype.onResize = function () {

    var device = this.app.graphicsDevice;
    var cw = device.width;
    var ch = device.height;
    var vw = this.video.videoWidth;
    var vh = this.video.videoHeight;
    
    this.ScreenResolutionText.element.text = 'Video feed : ' + this.video.videoWidth + ' x ' + this.video.videoHeight;  

    // Resize the video texture
    if (this.entity.model && this.entity.model.model) {
        var material = this.entity.model.model.meshInstances[0].material;
        material.setParameter('uVideoSize', new Float32Array([vw, vh]));
        material.setParameter('uCanvasSize', new Float32Array([cw, ch]));
    }
};

// MEGA HACK FOR IOS
// The video pauses when exiting fullscreen if the video element is not added to the DOM
GameMgr.prototype.addVideoToDom = function () {
    var style = this.video.style;
    style.position = 'absolute';
    style.width = '1%';
    style.height = '1%';

    document.body.appendChild(this.video);
};

GameMgr.prototype.useVideoTexture = function () {
    var device = this.app.graphicsDevice;

    // Create a texture to receive video frames. 888 format seems to achieve best performance.
    this.texture = new pc.Texture(device, {
        format: pc.PIXELFORMAT_R8_G8_B8
    });
    
    // Apply a linear filter to avoid pixelation of low resolution video
    this.texture.magFilter = pc.FILTER_LINEAR;
    this.texture.minFilter = pc.FILTER_LINEAR;
    this.texture.setSource(this.video);

    var shader = new pc.Shader(device, {
        attributes: {
            aPosition: pc.SEMANTIC_POSITION
        },
        vshader: [
            "attribute vec2 aPosition;",
            "",
            "uniform vec2 uCanvasSize;",
            "uniform vec2 uVideoSize;",
            "",
            "varying vec2 vUv0;",
            "",
            "void main(void)",
            "{",
            "    vUv0 = aPosition;",
            "    float vw = uVideoSize.x;",
            "    float vh = uVideoSize.y;",
            "    float va = uVideoSize.x / uVideoSize.y;",
            "    float cw = uCanvasSize.x;",
            "    float ch = uCanvasSize.y;",
            "    float ca = uCanvasSize.x / uCanvasSize.y;",
            "    if (ca < va)",
            "    {",
            "        vUv0.x *= ca / va;",
            "    }",
            "    else",
            "    {",
            "        vUv0.y *= va / ca;",
            "    }",
            "    vUv0 *= 0.5;",
            "    vUv0 += 0.5;",
            "    gl_Position = vec4(aPosition, 1.0, 1.0);",
            "}"
        ].join("\n"),
        fshader: [
            "precision " + device.precision + " float;",
            "",
            "varying vec2 vUv0;",
            "",
            "uniform sampler2D uVideoMap;",
            "",
            "void main(void)",
            "{",
            "    gl_FragColor = texture2D(uVideoMap, vUv0);",
            "}"
        ].join("\n")
    });

    // Create the vertex format
    var vertexFormat = new pc.VertexFormat(device, [
        { 
            semantic: pc.SEMANTIC_POSITION,
            components: 2,
            type: pc.ELEMENTTYPE_FLOAT32
        }
    ]);

    // Create a vertex buffer
    var vertexBuffer = new pc.VertexBuffer(device, vertexFormat, 4);

    // Fill the vertex buffer
    var vertexData = vertexBuffer.lock();
    var vertexDataF32 = new Float32Array(vertexData);
    vertexDataF32.set([-1, -1, 1, -1, -1, 1, 1, 1]);
    vertexBuffer.unlock();
    
    var mesh = new pc.Mesh();
    mesh.vertexBuffer = vertexBuffer;
    mesh.primitive[0] = {
        type: pc.PRIMITIVE_TRISTRIP,
        base: 0,
        count: 4,
        indexed: false
    };

    var material = new pc.Material();
    material.shader = shader;
    material.depthTest = false;
    material.depthWrite = false;
    material.setParameter('uVideoMap', this.texture);
    var cw = device.width;
    var ch = device.height;
    var vw = this.video.videoWidth;
    var vh = this.video.videoHeight;
    material.setParameter('uVideoSize', new Float32Array([vw, vh]));
    material.setParameter('uCanvasSize', new Float32Array([cw, ch]));

    var node = new pc.GraphNode();

    var meshInstance = new pc.MeshInstance(node, mesh, material);

    var model = new pc.Model();
    model.graph = node;
    model.meshInstances = [ meshInstance ];

    this.entity.addComponent('model', {
        type: 'asset',
        castShadows: false
    });
    
    this.entity.model.model = model;
};

GameMgr.prototype.startVideo = function () {
    this.useVideoTexture();

    // NASTY NASTY HACK
    if (pc.platform.ios)
        this.addVideoToDom();
};

GameMgr.prototype.supportsVideoMode = function () {
    return (navigator.mediaDevices && navigator.mediaDevices.getUserMedia);
};

GameMgr.prototype.enterVideoMode = function (success, error) {

    var self = this;

    var constraints = {
        audio: false,
        video: {
            // Prefer the rear camera
            facingMode: "users",
           
            width: { min: 640, ideal: 1080 },
            height: { min: 480, ideal: 720 }
        }
    };
    
    navigator.mediaDevices.getUserMedia(constraints).then(function (stream) {
        self.videoPlaying = false;
        
        // Create the video element to receive the camera stream
        var video = document.createElement('video');

        video.setAttribute('autoplay', '');
        video.setAttribute('muted', '');
        // This is critical for iOS or the video initially goes fullscreen
        video.setAttribute('playsinline', '');
        video.srcObject = stream;

        self.video = video;

        // Check for both video and canvas resizing
        // Changing screen orientation on mobile can change both!
        self.app.graphicsDevice.on('resizecanvas', function () {
            self.onResize();
        });
        video.addEventListener('resize', function () {
            self.onResize();
        });

        // Only play the video when it's actually ready
        video.addEventListener('canplay', function () {
            if (!self.videoPlaying) {
                self.startVideo();
                self.startOpenCV();
                self.videoPlaying = true;
                if (success) 
                    success();
            }
        });

        // iOS needs a user action to start the video
        if (pc.platform.mobile) {
            window.addEventListener('touchstart', function (e) {
                e.preventDefault();
                if (!self.videoPlaying) {
                    self.startVideo();
                    self.startOpenCV();
                    self.videoPlaying = true;
                    if (success)
                        success();
                }
            });
        }
        
        navigator.mediaDevices.enumerateDevices().then(function(devices) {
            self.app.fire('webcamera:ready');
            
            devices.forEach(function(device) {
                console.log(device.kind + ": " + device.label +
                            " id = " + device.deviceId);
            });
        }).catch(function (e) {
            self.app.fire('webcamera:ready');
            console.log("ERROR: Unable to acquire camera stream " + e);
        });
        
    }).catch(function(err) {
        self.app.fire('webcamera:ready');
        console.log(err.name + ": " + err.message);
    });
};

GameMgr.prototype.exitVideoMode = function () {
    // Tear down video resources
    if (this.video) {
        
        this.video.stop();
        this.src.delete();
        this.dst.delete();
        
        if (this.video.parentElement) {
            document.body.removeChild(this.video);
        }
    }
};

