// mesh-lines.js
var MeshLines = pc.createScript('meshLines');

// initialize code called once per entity
MeshLines.prototype.postInitialize = function() {
    var app = this.app;
    var i;
    
    var mesh = new pc.Mesh(app.graphicsDevice);
    mesh.clear(true, false);

    var node = new pc.GraphNode();
    var material = new pc.StandardMaterial();  
    material.emissive = pc.Color.WHITE;
    material.emissiveIntensity = 1;

    // Draw a line
    var lineCount = 20;
    var spacing = 0.1;
    this.positions = new Float32Array(3 * 2 * lineCount);

    for (i = 0; i < lineCount; i++) {
        var startIndex = (i * 2 * 3);
        var endIndex = (((i * 2) + 1) * 3);
        
        var pos = this.entity.getPosition();
        // x, y, z
        this.positions[startIndex + 0] = pos.x - 1; 
        this.positions[startIndex + 1] = pos.y + (i * spacing);
        this.positions[startIndex + 2] = pos.z;

        this.positions[endIndex + 0] = pos.x + 1; 
        this.positions[endIndex + 1] = pos.y + (i * spacing);
        this.positions[endIndex + 2] = pos.z;
    }

    this.indexArray = [];
    for (i = 0; i < lineCount; i++) {
        this.indexArray.push(i * 2);
        this.indexArray.push((i * 2) + 1);
    }

    mesh.setPositions(this.positions);
    mesh.setNormals(pc.calculateNormals(this.positions, this.indexArray));
    mesh.setIndices(this.indexArray);
    mesh.update(pc.PRIMITIVE_LINES);

    var meshInstance = new pc.MeshInstance(node, mesh, material);
    
    // Create a model and add the mesh instance to it
    var model = new pc.Model();
    model.graph = node;
    model.meshInstances = [meshInstance];

    // Create Entity and add it to the scene

    // Add a model compoonent
    app.systems.model.addComponent(this.entity, {
        type: 'asset'
    });
    
    var myLayer = this.app.scene.layers.getLayerByName("RenderToTexture");
    this.entity.model.layers = [myLayer.id];
    this.entity.model.model = model;
    this.entity.model.model.meshInstances[0].material = material;    
};

// update code called every frame
MeshLines.prototype.update = function(dt) {
    
};

