// HeartBeatRenderer.js
var HeartBeatRenderer = pc.createScript('heartBeatRenderer');

HeartBeatRenderer.attributes.add("layerName", {type: "string"});
HeartBeatRenderer.attributes.add('heart', { type: 'entity' });

// initialize code called once per entity
HeartBeatRenderer.prototype.initialize = function() {
    this.RTTlayer = this.app.scene.layers.getLayerById(this.layerName);
    
    this.fig = new matplot.Figure("plot", 300, 200);
    this.ax = this.fig.axes();
    
    this.pos = [];
    this.time = []; 
};

HeartBeatRenderer.prototype.setArrays = function(positions, times, lowest, highest, outBeat) { 
    this.ax.plot(times, positions, {color: 'blue', linewidth: 2});
    this.fig.draw(lowest-outBeat*0.5, highest+outBeat*0.5);
};


